.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 325px;
    min-height: 100vh;
    max-height: 100vh;
    background: linear-gradient(to bottom,#00c4cc,#7d2ae8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 1px 0 8px grey;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
    overflow: hidden;
}

.sidebar.open {
    transform: translateX(0);
}

/* horizontally center  */
.sidebar_content {
    margin: 0 auto;
}

.sidebar_logo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
}

.sidebar_logo:hover {
    box-shadow: 0 8px 2px rgba(0,0,0,0.12), 0 8px 3px rgba(0,0,0,0.12);
    transition: all 0.5s cubic-bezier(.25,.8,.25,1);
}

.sidebar_content p {
    color: #fff;
    text-align: center;
    font-size: 1.7rem;
    margin-top: 0.9rem;
    margin-bottom: 0;
    font-weight: 300;
}

.sidebar_content h4 {
    color: rgb(245, 245, 245);
    font-size: 1.1rem;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    font-weight: 200;
}

.sidebar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;
}

.sidebar_navigation-items ul li {
    padding: 5px 0;
    background: linear-gradient(330deg, #1e8ce0, #00b1a2) no-repeat;
    text-align: center;
    font-size: 1.2rem;
    color: white;
}

.sidebar_navigation-items ul a {
    text-decoration: none;
}

.sidebar_navigation-items ul li:hover {
    background: #163958;
}

.sidebar_navigation-items ul a li:hover {
    color: rgb(211, 211, 211);
}

.sidebar_socialmedia_icons {
    margin: 0.8rem 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.sidebar_socialmedia_icons a {
    width: 45px !important;
    height: 45px !important;
    margin: 0 0.25rem;
}

.sidebar_socialmedia_icons .social-svg-background {
    fill: white !important;
}

.sidebar_socialmedia_icons .social-svg-mask {
    fill: #2b435a !important;
}

@media only screen and (max-width: 1279px) and (min-width: 1024px){
    .sidebar {
        width: 275px;
    }
}

@media only screen and (max-width: 1024px){
    .sidebar {
        width: 225px;
    }

    .sidebar_logo {
        width: 120px;
        height: 120px;
    }

    .sidebar_content p {
        font-size: 1.25rem;
    }
    
    .sidebar_content h4 {
        font-size: 0.9rem;
    }

    .sidebar_navigation-items ul li {
        font-size: 1rem;
    }

    .sidebar_socialmedia_icons a {
        width: 35px !important;
        height: 35px !important;
    }
}

