.page-main-content p {
    font-size: 1.1rem;
    font-family: 'Merriweather', serif;
    color: hsl(0, 0%, 31%);
    line-height: 1.8rem;
    letter-spacing: -.3px;
}

.page-main-content h1 {
    font-size: 1.8rem;
}

.page-main-content ul li {
    font-size: 1.1rem;
    font-family: 'Merriweather', serif;
}

.page-main-content .experience {
    padding-top: 0.02rem;
    margin-right: 12rem;
}

.page-main-content .experience h3 {
    font-family: 'Merriweather', serif;
    font-weight: 600;
    font-size: 1.1rem;
    letter-spacing: -.5px;
}

.page-main-content .experience .position {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.page-main-content .experience .position h5 {
    font-family: 'Merriweather', serif;
    font-weight: 500;
    font-size: 1.1rem;
    letter-spacing: -.8px;
    margin-top: 0;
    color: rgb(114, 105, 105);
    margin-bottom: 0.8rem;
}

.page-main-content .experience ul {
    margin-top: 0;
    margin-left: 0;
}

.page-main-content .experience ul li {
    font-family: 'Merriweather', serif;
    font-weight: 500;
    letter-spacing: -.3px;
}

@media only screen and (max-width: 1279px) {
    .page-main-content .experience {
        margin-right: 0;
    }

    .page-main-content h1 {
        font-size: 1.5rem;
    }

    .page-main-content .experience .position h5 {
        font-size: 1rem;
    }

    .page-main-content .experience ul li {
        font-size: 1rem;
    }
}