body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Merriweather Sans"
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 325px;
    min-height: 100vh;
    max-height: 100vh;
    background: -webkit-linear-gradient(top,#00c4cc,#7d2ae8);
    background: linear-gradient(to bottom,#00c4cc,#7d2ae8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 1px 0 8px grey;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    overflow: hidden;
}

.sidebar.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

/* horizontally center  */
.sidebar_content {
    margin: 0 auto;
}

.sidebar_logo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
}

.sidebar_logo:hover {
    box-shadow: 0 8px 2px rgba(0,0,0,0.12), 0 8px 3px rgba(0,0,0,0.12);
    -webkit-transition: all 0.5s cubic-bezier(.25,.8,.25,1);
    transition: all 0.5s cubic-bezier(.25,.8,.25,1);
}

.sidebar_content p {
    color: #fff;
    text-align: center;
    font-size: 1.7rem;
    margin-top: 0.9rem;
    margin-bottom: 0;
    font-weight: 300;
}

.sidebar_content h4 {
    color: rgb(245, 245, 245);
    font-size: 1.1rem;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    font-weight: 200;
}

.sidebar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;
}

.sidebar_navigation-items ul li {
    padding: 5px 0;
    background: -webkit-linear-gradient(120deg, #1e8ce0, #00b1a2) no-repeat;
    background: linear-gradient(330deg, #1e8ce0, #00b1a2) no-repeat;
    text-align: center;
    font-size: 1.2rem;
    color: white;
}

.sidebar_navigation-items ul a {
    text-decoration: none;
}

.sidebar_navigation-items ul li:hover {
    background: #163958;
}

.sidebar_navigation-items ul a li:hover {
    color: rgb(211, 211, 211);
}

.sidebar_socialmedia_icons {
    margin: 0.8rem 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.sidebar_socialmedia_icons a {
    width: 45px !important;
    height: 45px !important;
    margin: 0 0.25rem;
}

.sidebar_socialmedia_icons .social-svg-background {
    fill: white !important;
}

.sidebar_socialmedia_icons .social-svg-mask {
    fill: #2b435a !important;
}

@media only screen and (max-width: 1279px) and (min-width: 1024px){
    .sidebar {
        width: 275px;
    }
}

@media only screen and (max-width: 1024px){
    .sidebar {
        width: 225px;
    }

    .sidebar_logo {
        width: 120px;
        height: 120px;
    }

    .sidebar_content p {
        font-size: 1.25rem;
    }
    
    .sidebar_content h4 {
        font-size: 0.9rem;
    }

    .sidebar_navigation-items ul li {
        font-size: 1rem;
    }

    .sidebar_socialmedia_icons a {
        width: 35px !important;
        height: 35px !important;
    }
}


.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 3px;
    background: black;
}

.drawer-toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.toolbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 70px;
    background: -webkit-linear-gradient(340deg,#00e4e4,#873dfd) no-repeat;
    background: linear-gradient(110deg,#00e4e4,#873dfd) no-repeat;
    padding-left: 25px;
}

.toolbar h1 {
    font-size: 1.85rem;
    margin-left: 2rem;
    color: #333;
    font-weight: 600;
}

@media only screen and (max-width: 1279px) and (min-width: 1024px) {
    .toolbar h1 {
        font-size: 1.75rem;
    }
}

@media only screen and (max-width: 1024px){
    .toolbar h1 {
        font-size: 1.75rem;
    }
}

.page-main-content p {
    font-size: 1.1rem;
    font-family: 'Merriweather', serif;
    color: hsl(0, 0%, 31%);
    line-height: 1.8rem;
    letter-spacing: -.3px;
}

.page-main-content h1 {
    font-size: 1.8rem;
}

.page-main-content ul li {
    font-size: 1.1rem;
    font-family: 'Merriweather', serif;
}

.page-main-content .experience {
    padding-top: 0.02rem;
    margin-right: 12rem;
}

.page-main-content .experience h3 {
    font-family: 'Merriweather', serif;
    font-weight: 600;
    font-size: 1.1rem;
    letter-spacing: -.5px;
}

.page-main-content .experience .position {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.page-main-content .experience .position h5 {
    font-family: 'Merriweather', serif;
    font-weight: 500;
    font-size: 1.1rem;
    letter-spacing: -.8px;
    margin-top: 0;
    color: rgb(114, 105, 105);
    margin-bottom: 0.8rem;
}

.page-main-content .experience ul {
    margin-top: 0;
    margin-left: 0;
}

.page-main-content .experience ul li {
    font-family: 'Merriweather', serif;
    font-weight: 500;
    letter-spacing: -.3px;
}

@media only screen and (max-width: 1279px) {
    .page-main-content .experience {
        margin-right: 0;
    }

    .page-main-content h1 {
        font-size: 1.5rem;
    }

    .page-main-content .experience .position h5 {
        font-size: 1rem;
    }

    .page-main-content .experience ul li {
        font-size: 1rem;
    }
}
html {
    height: 100%;
}

body {
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    font-family: Helvetica, sans-serif;
    height: 100%;
}

.main-content { 
    height: 100%;
    -webkit-transition: margin-left .5s;
    transition: margin-left .5s;
}

.main-content-push-left {
    margin-left: 325px !important;
}

.main-content-mobile-push-left {
    margin-left: 275px !important;
}

.page-content {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1;
    overflow: auto;
}

.page-main-content {
    margin: 3rem 35rem 0 5.5rem;
}

@media only screen and (max-width: 1279px) and (min-width: 1024px) {
    .main-content-push-left {
        margin-left: 275px !important;
    }

    .page-main-content {
        margin: 3rem 30rem 0 2rem;
    }
}

@media only screen and (max-width: 1024px){
    .main-content-push-left {
        margin-left: 225px !important;
    }

    .page-main-content {
        margin: 3rem 1rem 0 1.5rem;
    }
}

