.toolbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 70px;
    background: linear-gradient(110deg,#00e4e4,#873dfd) no-repeat;
    padding-left: 25px;
}

.toolbar h1 {
    font-size: 1.85rem;
    margin-left: 2rem;
    color: #333;
    font-weight: 600;
}

@media only screen and (max-width: 1279px) and (min-width: 1024px) {
    .toolbar h1 {
        font-size: 1.75rem;
    }
}

@media only screen and (max-width: 1024px){
    .toolbar h1 {
        font-size: 1.75rem;
    }
}
